@font-face {
  font-family: 'iconfont';
  src: url("//at.alicdn.com/t/font_1460528711_8168766.eot"), url("//at.alicdn.com/t/font_1460528711_8168766.woff") format('woff'), url("//at.alicdn.com/t/font_1460528711_8168766.ttf") format('truetype'), url("//at.alicdn.com/t/font_1460528711_8168766.svg#iconfont") format('svg');
}
.iconfont {
  font-family: "iconfont" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
details,
section,
summary,
aside,
main,
menu,
nav,
figcaption,
figure,
footer,
header,
hgroup {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
b,
strong {
  font-weight: bold;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  cursor: pointer;
  -webkit-appearance: button;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type='checkbox'],
input[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  height: auto;
}
input[type='search'] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
.iconfont {
  font-size: 16px;
}
[data-dpr="2"] .iconfont {
  font-size: 32px;
}
[data-dpr="3"] .iconfont {
  font-size: 48px;
}
.stuff {
  height: 1.333333333333333rem;
}
.user-banner {
  height: 3.333333333333333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-pack: center;
  -o-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  background-color: #f60;
  color: #eee;
  position: relative;
}
.user-banner .face {
  position: absolute;
  height: 1.6rem;
  width: 1.6rem;
  border-radius: 100%;
  top: 65%;
  margin-left: -0.8rem;
  left: 50%;
  background-size: 100% 100%;
}
.section-order {
  background-color: #fff;
  base-text-color: #fff;
  text-align: center;
  line-height: 1.3;
}
.section-order .flex {
  padding: 0.533333333333333rem 0 0.266666666666667rem 0;
  font-size: 12px;
}
[data-dpr="2"] .section-order .flex {
  font-size: 24px;
}
[data-dpr="3"] .section-order .flex {
  font-size: 36px;
}
.section-order .flex i {
  display: block;
  font-size: 22px;
}
[data-dpr="2"] .section-order .flex i {
  font-size: 44px;
}
[data-dpr="3"] .section-order .flex i {
  font-size: 66px;
}
.flex-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: box;
  display: flex;
}
.flex {
  -webkit-box-flex: 1;
  -o-box-flex: 1;
  box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.ui-border-b {
  border-bottom: 1px solid #ddd;
}
.section-block {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  margin: 0.266666666666667rem 0;
}
.section-list {
  margin: 0;
  padding: 0;
}
.section-list .cell {
  padding: 0.266666666666667rem 0.266666666666667rem 0.266666666666667rem 0.533333333333333rem;
  position: relative;
  font-size: 16px;
}
[data-dpr="2"] .section-list .cell {
  font-size: 32px;
}
[data-dpr="3"] .section-list .cell {
  font-size: 48px;
}
.section-list .cell:before,
.section-list .cell:after {
  content: '';
  display: block;
  position: absolute;
}
.section-list .cell:before {
  width: 100%;
  left: 0.533333333333333rem;
  bottom: 0;
  height: 1px;
  background-color: #ddd;
}
.section-list .cell:after {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  height: 0.133333333333333rem;
  width: 0.133333333333333rem;
  border-width: 2px 2px 0 0;
  border-color: #c8c8cd;
  border-style: solid;
  right: 0.266666666666667rem;
  top: 45%;
}
.section-list .cell:last-child:before {
  display: none;
}
.user-list li:first-child i {
  color: #9d34bf;
}
.user-list li:nth-child(2) i {
  color: #41bd21;
}
.user-list li:nth-child(3) i {
  color: #ea8631;
}
.user-list li:nth-child(4) i {
  color: #3195ea;
}
.user-list2 li:first-child i {
  color: #c76f23;
}
.user-list2 li:nth-child(2) i {
  color: #485858;
}


